<template>
    <div class="menu">
        <Table :columns="table.columns" :data="table.data"></Table>
    </div>
</template>
<script>
  export default {
    data () {
      return {
        value1: '1',
        table:{
          columns:[
            {
              title: '名称',
              key: 'Name'
            },
            {
              title: '链接',
              key: 'Url'
            },
            {
              title:"操作",
              key:"Option"
            }
          ],
          data:{}
        }
      }
    },
    created () {
      this.$http.get("/console/menu/get").then(function (data) {
        data=data.body;
        let n=0;
        for(let i in data){
          this.table.data[n]={
            Name:data[i]["name"],
            Url:data[i]["url"],
            Option:"<Button type=\"error\">删除</Button><Button type=\"info\">编辑</Button>"
          }
          n++;
        }
        this.table.data[n]={
          Name:"<Input/>",
          Url:"<Input/>",
          Option:"<Button type=\"success\">添加</Button>"
        }
        //vm.$set(this.aas,3,this.aas[3])
      },function(error){
        alert("请求失败");
      });
    }
  }
</script>
